import React, { useState } from 'react';
import SectionTitle from '../../components/SectionTitle'
import { TabContent, TabPane, Nav, NavItem, NavLink } from 'reactstrap';
import classnames from 'classnames';
import sImg1 from '../../images/us/2020-04-25_20.42.10_small.jpg'
import sImg2 from '../../images/us/2020-04-25_20.42.10_small.jpg'
import sImg3 from '../../images/us/2020-04-25_20.42.10_small.jpg'
import sImg4 from '../../images/us/2020-04-25_20.42.10_small.jpg'

import sIcon1 from '../../images/story/1.png'
import sIcon2 from '../../images/story/2.png'
import sIcon3 from '../../images/story/3.png'
import sIcon4 from '../../images/story/4.png'


import Shape from '../../images/story/shape.jpg'


const StorySection = (props) => {

    const [activeTab, setActiveTab] = useState('1');

    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }


    const Tabs = [
        {
            Id: '1',
            tabItem: 'Første møte',
        },
        {
            Id: '2',
            tabItem: 'Første date',
        },
        {
            Id: '3',
            tabItem: 'Frieri',
        },
        {
            Id: '4',
            tabItem: 'Vår forlovelse',
        }
    ]

    const Story = [
        {
            Id: '1',
            sImg: sImg1,
            sIcon: sIcon1,
            Shape: Shape,
            sTitle: 'Første gang vi møttes',
            date: 'Høsten 2007',
            description: 'Vi jobbet begge i Objectnet/Unified, og Alf Kristian hadde hørt mye om hun flotte Anita. Så han var faktisk ganske spent da han møtte henne på prosjekt på Telenor. Han skjønte fort at her måtte det satses!'
        },
        {
            Id: '2',
            sImg: sImg2,
            sIcon: sIcon2,
            Shape: Shape,
            sTitle: 'Første date',
            date: 'Våren 2008',
            description: 'Vi skulle møtes til en kaffe, men det ble en lang kveld! Selv om vi diskuterte og var uenige om enkelte ting, ble vi nok begge ganske forelska. Men det tok 3 dater til første kyss.'
        },
        {
            Id: '3',
            sImg: sImg3,
            sIcon: sIcon3,
            Shape: Shape,
            sTitle: 'Frieri',
            date: '22. desember 2019',
            description: 'Ring i champagneglass, og ned på kne. Anita var nok sjanseløs! Feiret med fantastisk middag ut.'
        },
        {
            Id: '4',
            sImg: sImg4,
            sIcon: sIcon4,
            Shape: Shape,
            sTitle: 'Forlovelsestid',
            date: '22. desember 2019 - i idag!',
            description: 'Vi skulle gjerne giftet oss før, men dette har vært noen spesielle år. Vi er så glad for å kunne gjennomføre dette nå sammen med dere.'
        }
    ]



    return (

        <section className="wpo-story-section section-padding" id="story">
            <div className="container">
                <SectionTitle MainTitle={'Vår historie'} />
                <div className="row align-items-center justify-content-center">
                    <div className="col col-lg-12 col-12">
                        <div className="tab-area">
                            <div className="tablinks">
                                <Nav tabs className="service-thumbs-outer">
                                    {Tabs.map((tab, titem) => (
                                        <NavItem key={titem}>
                                            <NavLink
                                                className={`service-thumb ${classnames({ active: activeTab === tab.Id })}`}
                                                onClick={() => { toggle(tab.Id); }}
                                            >
                                                {tab.tabItem}
                                                <span className="number">{tab.tabNumber}</span>
                                            </NavLink>
                                        </NavItem>
                                    ))}
                                </Nav>
                            </div>
                            <TabContent activeTab={activeTab}>
                                {Story.map((story, Sitem) => (
                                    <TabPane tabId={story.Id} key={Sitem}>
                                        <div className="wpo-story-item">
                                            <div className="wpo-story-img">
                                                <img src={story.sImg} alt="" />
                                            </div>
                                            <div className="wpo-story-content">
                                                <div className="wpo-story-content-inner">
                                                    <span><img src={story.sIcon} alt="" /></span>
                                                    <h2>{story.sTitle}</h2>
                                                    <span>{story.date}</span>
                                                    <p>{story.description}</p>
                                                    <div className="border-shape">
                                                        <img src={story.Shape} alt="" />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </TabPane>
                                ))}
                            </TabContent>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}


export default StorySection;