import React from 'react'
import SectionTitle from '../SectionTitle'
import sImg1 from '../../images/us/middag_fjord_45174258902_o_small.jpg'
import sImg2 from '../../images/us/the-thief_15383386785_o.jpg'
import sImg3 from '../../images/us/the-thief_15196595058_o_small.jpg'
import LocationMap from '../Modal'
import { Link } from "react-router-dom";


const Events = [
    {
        Simg: sImg1,
        title: 'Mottakelse',
        li1: 'Lørsdag 11. juni kl 14',
        li2: 'Roof Grill, på taket av hotellet.',
    },
    {
        Simg: sImg2,
        title: 'Insjekking',
        li1: 'Lørsdag 11. juni kl 15',
        li2: 'Resepsjonen på The Thief.',
    },
    {
        Simg: sImg3,
        title: 'Fest og middag',
        li1: 'Lørsdag 11. juni kl 16',
        li2: 'Roof Grill, på taket av hotellet.',
    },

]

const EventSection = (props) => {
    return (
        <section className={`wpo-event-section section-padding ${props.eClass}`} id="events">
            <div className="container">
                <SectionTitle MainTitle={'Tid og sted'} />
                <div className="wpo-event-wrap">
                    <div className="row">
                        {Events.map((event, eitem) => (
                            <div className="col col-lg-4 col-md-6 col-12" key={eitem}>
                                <div className="wpo-event-item">
                                    <div className="wpo-event-img" style={{ overflow: "hidden", height: "178px" }}>
                                        <img src={event.Simg} alt="" />
                                    </div>
                                    <div className="wpo-event-text">
                                        <h2>{event.title}</h2>
                                        <ul>
                                            <li>{event.li1}</li>
                                            <li>{event.li2}</li>
                                            <li><LocationMap /></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                    <div className='row'>
                        <p style={{ textAlign: "center" }}>
                            For detaljert informasjon, sjekk ut <Link to="/practical-info">praktisk informasjon</Link>.
                        </p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default EventSection;