import React, { Component } from 'react'
import SectionTitle from '../../components/SectionTitle'

import vec1 from '../../images/contact/1.png'
import vec2 from '../../images/contact/2.png'

class RSVP extends Component {

    render() {

        return (
            <section className={`wpo-contact-section ${this.props.pt}`} id="RSVP">
                <div className="container">
                    <div className="wpo-contact-section-wrapper">
                        <div className="wpo-contact-form-area">
                            <SectionTitle MainTitle={'Skal du være med?'} />
                            <div className="border-style" style={{ textAlign: "center", marginBottom: "22px" }}>
                                Vi vil be deg gi oss beskjed om du skal være med eller ikke, og hvor mange dere blir
                                innen lørdag 2. april 2022. Send oss gjerne en epost til <a href="mailto:anita-og-alf@stoyle.eu">anita-og-alf@stoyle.eu</a>.
                            </div>
                            <div className="border-style" style={{ textAlign: "center" }}>
                                Vi håper du vil bo på hotellet, og ber deg ta kontakt med dem før denne dato også.
                            </div>
                        </div>
                        <div className="vector-1">
                            <img src={vec1} alt="" />
                        </div>
                        <div className="vector-2">
                            <img src={vec2} alt="" />
                        </div>
                    </div>
                </div>
            </section>
        )
    }

}
export default RSVP;