import React from 'react'
import { Link } from 'react-router-dom'
import Projects from '../../api/projects'


const Footer = (props) => {

    const ClickHandler = () => {
        window.scrollTo(10, 0);
    }

    return (
        <footer className="wpo-site-footer">
            <div className="wpo-upper-footer">
                <div className="container">
                    <div className="row">
                        <div className="col col-xl-3 col-lg-4 col-md-6 col-sm-12 col-12">
                            <div className="widget about-widget">
                                <div className="logo widget-title">
                                    <Link onClick={ClickHandler} className="logo" to="/">Bryllup</Link>
                                </div>
                                <p>Vi håper du vil dele denne spesielle dagen med oss!</p>
                            </div>
                        </div>
                        <div className="col col-xl-3  col-lg-4 col-md-6 col-sm-12 col-12">
                            <div className="widget link-widget">
                                <div className="widget-title">
                                    <h3>Informasjon</h3>
                                </div>
                                <ul>
                                    Mer informasjon, sjekk ut <Link to="/practical-info">praktisk informasjon</Link>.
                                </ul>
                            </div>
                        </div>
                        <div className="col col-xl-3  col-lg-4 col-md-6 col-sm-12 col-12">
                            <div className="widget wpo-service-link-widget">
                                <div className="widget-title">
                                    <h3>Kontakt </h3>
                                </div>
                                <div className="contact-ft">
                                    <p>Har du noen spørsmål ta gjerne kontakt.</p>
                                    <ul>
                                        <li><i className="fi flaticon-email"></i><a href="anita-og-alf@stoyle.eu">anita-og-alf@stoyle.eu</a></li>
                                        <li><i className="fi flaticon-phone-call"></i><a href="tel:+47 997 92 226">+47 997 92 226</a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            <div className="wpo-lower-footer">
                <div className="container">
                    <div className="row">
                        <div className="col col-xs-12">
                            <p className="copyright"> &copy; 2022 Anita og Alf Kristian. All Rights Reserved.</p>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer;