import React from 'react'
import SectionTitle from '../SectionTitle'
import pImg1 from '../../images/us/DSC_0658_small.JPG'
import pImg2 from '../../images/us/P5180345_small.JPG'
import pImg3 from '../../images/us/IMG_1175_small.JPG'
import pImg4 from '../../images/us/IMG_1232_small.JPG'
import pImg5 from '../../images/us/2021-02-06_14.37.03_small.jpg'
import pImg6 from '../../images/us/2021-02-06_18.42.35_1_small.jpg'
import pImg7 from '../../images/us/2021-03-01_17.18.47_small.jpg'
import pImg8 from '../../images/us/2022-01-31_14.48.38_small.jpg'
import pImg9 from '../../images/us/DSC_0601_small.JPG'
import ReactFancyBox from 'react-fancybox'
import 'react-fancybox/lib/fancybox.css'


const Portfolios = [
    {
        Pimg: pImg1,
    },
    {
        Pimg: pImg2,
    },
    {
        Pimg: pImg3,
    },
    {
        Pimg: pImg4,
    },
    {
        Pimg: pImg5,
    },
    {
        Pimg: pImg6,
    },
    {
        Pimg: pImg7,
    },
    {
        Pimg: pImg8,
    },
    {
        Pimg: pImg9,
    },
]

const PortfolioSection2 = (props) => {
    return (
        <section className={`wpo-portfolio-section section-padding ${props.pClass}`} id="gallery">
            <div className="container">
                <SectionTitle MainTitle={'Noen øyeblikk med oss'} />
                <div className="sortable-gallery">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="portfolio-grids gallery-container clearfix">
                                {Portfolios.map((portfolio, pitem) => (
                                    <div className="grid" key={pitem}>
                                        <div className="img-holder">
                                            <ReactFancyBox
                                                thumbnail={portfolio.Pimg}
                                                image={portfolio.Pimg}
                                            />
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default PortfolioSection2;