import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import coupleImg1 from '../../images/us/anita_og_meg_magnus_bursdag_small.JPG'

class CoupleSection extends Component {

    render() {
        return (
            <section className="couple-section section-padding" id="couple">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col col-xs-12">
                            <div className="couple-area clearfix">
                                <div className="text-grid bride">
                                    <h3>Anita Andersen</h3>
                                    <p>Gode, snille Anita, skal endelig få sin drømmeprins!</p>
                                    {/* <div className="social">
                                        <ul>
                                            <li><Link to="/"><i className="ti-facebook"></i></Link></li>
                                            <li><Link to="/"><i className="ti-twitter-alt"></i></Link></li>
                                            <li><Link to="/"><i className="ti-instagram"></i></Link></li>
                                        </ul>
                                    </div> */}
                                </div>
                                <div className="middle-couple-pic">
                                    <div className="middle-couple-pic-inner">
                                        <img src={coupleImg1} alt="" style={{ objectFit: "cover" }} />
                                    </div>
                                </div>
                                <div className="text-grid groom">
                                    <h3>Alf Kristian Støyle</h3>
                                    <p>Tøffingen Alf Kristian, skal endelig få drømmedama!</p>
                                    {/* <div className="social">
                                        <ul>
                                            <li><Link to="/"><i className="ti-facebook"></i></Link></li>
                                            <li><Link to="/"><i className="ti-twitter-alt"></i></Link></li>
                                            <li><Link to="/"><i className="ti-instagram"></i></Link></li>
                                        </ul>
                                    </div> */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        );
    }
}

export default CoupleSection;