import React from 'react'
import { Link } from 'react-router-dom'
import erimg from '../../images/error-404.svg'


const Error = (props) => {
    return (
        <section className="error-404-section section-padding">
            <div className="container">
                <div className="row">
                    <div className="col col-xs-12">
                        <div className="content clearfix">
                            <div className="error">
                                <img src={erimg} alt="" />
                            </div>
                            <div className="error-message">
                                <h3>Oops! Denne siden eksisterer ikke!</h3>
                                <p>Hvordan kom du hit mon tro? Si gjerne ifra til brudgommmen, så kanskje han kan hjelpe deg!</p>
                                <Link to="/home" className="theme-btn-s3">Tilbake til forsiden</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Error;